import React from 'react';

const Posts = (props) => {
    return (
        <div className="posts">
            <div className="post">
                <div className="image">
                    <img src= {props.url} alt="" />
                </div>
                <div className="text">
                <p>{props.para}</p>
                <h1>{props.heading}</h1>
                {/* <i class="fas fa-ellipsis-v"></i> */}
                <h3>{props.desc}</h3>
                <div className="lower">
                    <div className="name">
                        <div className="profile"></div>
                            <p>{props.name}</p>
                        </div>
                        <div className="views">
                            <i class="far fa-eye"></i><span>{props.views}</span>
                            <i class="fas fa-share-alt"></i>
                        </div>
                </div>
                <div className="lower2">
                    <div className="name">
                        <div className="profile"></div>
                            <div className="mobile">
                            <p>{props.name}</p>
                            <span>{props.views}</span>
                            </div>
                        </div>
                        <div className="views">
                            <i class="fas fa-share-alt"> Share</i>
                        </div>
                </div>
                </div>
            </div>
            
        </div>

    )
}

export default Posts
