import React from 'react'
import Posts from './Posts'
import Right from './Right'
import bg from "./images/post1.jpg";
import bg2 from "./images/post2.jpg";
import bg3 from "./images/post4.jpg";
const Body = () => {
    return (
        <>
            <div className="body">
                <div className="container">
                <ul>
                    <li className="active">Posts(32)</li>
                    <li>Article</li>
                    <li>Event</li>
                    <li>Education</li>
                    <li>Job</li>
                </ul>
                </div>
                <div className="right">
                    <div className="rectangle">
                        <p>Write a Post</p>
                        <i class="fas fa-caret-down"></i>
                    </div>
                    <div className="groups">
                        <i class="fas fa-users"></i>
                        <p>Join Group</p>
                    </div>
                </div>
            </div>
            <div className="mobile-view">
                <div className="container">
                <ul>
                    <li className="active">Posts(32)</li>
                </ul>
                </div>
                <div className="right">
                    <div className="rectangle">
                        <p>Filter:All</p>
                        <i class="fas fa-caret-down"></i>
                    </div>
                </div>
            </div>
            <hr />
            <div className="body2">
            <div className="left">
            <div><Posts para = "✍️ Article" url = {bg} heading = "What if famous brands had regular fonts? Meet RegulaBrands!" desc = "I’ve worked in UX for the better part of a decade. From now on, I plan to rei…" name = "Sarthak Kamra" views = "1.4k views"   /></div>
            <div><Posts para = "🔬️ Education" url = {bg2} heading = "Tax Benefits for Investment under National Pension Scheme launched by Government" desc = "I’ve worked in UX for the better part of a decade. From now on, I plan to rei…" name = "Sarah West" views = "1.4k views"   /></div>
            <div><Posts para = "🗓️ Meetup" url ={bg3} heading = "Finance & Investment Elite Social Mixer @Lujiazui"  name = "Ronal Jones" views = "1.4k views" desc = "I’ve worked in UX for the better part of a decade. From now on, I plan to rei…"  /></div>
            </div>
            <div className="rightpart">
            <Right />
            </div>
            </div>
        </>
    )
}

export default Body
