import React from 'react'

const Right = () => {
    return (
        <>
            <div className="righter">
            <i class="fas fa-map-marker-alt"><span>Noida, India</span></i>
            <i class="fas fa-pen"></i>
            <hr className="rightline" />
            <i class="fas fa-exclamation-circle"><span>Your location will help us serve better and extend a personalised experience.</span></i>
            </div>
        </>
    )
}

export default Right
